/* add css module styles here (optional) */

._2pRR5 {
  box-shadow: 0px 0px 10px #333;
  border-radius: 8px;
  text-align: center;
  float: left;
  z-index: 300;
  background-color: white;
  margin: 0% auto;
  color: #333 !important;

  position: fixed;
  width: 600px;
  top: 10%;
  left: calc(50% - 300px);
  z-index: 300;

  transform: scale(0.5);

  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

._2pRR5._3kLFl {
  transform: scale(1);
  opacity: 1;
  visibility: visible;
}

._2pRR5._1cbGo {
  transform: scale(0.9);
  opacity: 0;
  z-index: -1000;
}

._2b0Yz {
  font-size: 16px;
  outline: none;
  border: none;
  width: 90%;
  padding-right: 0px;
  margin-right: 0px;
  margin-left: 0px;
  padding: 18px 4px 18px 40px;
  padding-left: 16px;
  padding-bottom: 0px;
  background: none;
}

._RR1M_ {
  font-size: 11px;
  margin-right: 8px;
  cursor: pointer;
}

._RR1M_:hover {
  opacity: 0.6;
}

._rVwcX {
  flex-direction: row;
  display: flex;
  margin-left: 20px;
  margin-top: 12px;
  cursor: pointer;
}

._1tmih {
  outline: solid 2px #333;
  border: none;
  background-color: #444;
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 2px;
  padding-bottom: 2px;
  font-weight: bold;
  color: white;
  border-radius: 100px;
  text-transform: uppercase;
  font-size: 11px;
  transition: all 0.2s ease;
  cursor: pointer;
}

._1tmih:hover {
  background-color: white;
  color: #333;
  outline: solid 2px #333;
  transition: all 0.2s ease;
}

._3Mno_ {
  text-align: left;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 20px;
  flex-direction: row;
  display: flex;
  cursor: pointer;
  font-size: 12px;
  background-color: red;
}

._3Mno_._3G4VH {
  background-color: #f4f4f4;
  transition: all 0.1s ease-in;
  opacity: 1;
}

._3Mno_._3rFqu {
  transition: all 0.3s ease-out;
  opacity: 1;
  background-color: white;
}

._2qZ4G {
  transition: all 0.3s ease-out;
  padding: 4px;
  padding-left: 8px;
  padding-right: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 9px;
  text-align: left;
  margin-left: 20px;
  opacity: 0.7;
  cursor: pointer;
}

._2qZ4G:hover {
  transition: all 0.3s ease-out;
  opacity: 1;
}
